import React from "react";
import styled, { keyframes, css } from "styled-components";
import "./App.css";
import "bootstrap";
import "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
function Our_Clients() {
  const row1 = [
    "https://www.filepicker.io/api/file/zHuNTzcS76RfMr9gOUWq",
    "https://seeklogo.com/images/A/asahi-india-logo-428D2838CA-seeklogo.com.png",
    "https://www.pngkey.com/png/detail/500-5001699_autoliv-logo-black-and-white.png",
    "https://file.mk.co.kr/meet/neds/2021/02/image_readtop_2021_105330_16121629594527020.jpg",
    "https://cdn.worldvectorlogo.com/logos/fag-1.svg",
    "https://upload.wikimedia.org/wikipedia/commons/2/24/Logo_de_Faurecia.svg",
    "data:image/png;base64,",
  ];

  const row2 = [
    "https://www.gpb.org/sites/default/files/styles/flexheight/public/blogs/images/2014/08/19/hanil_1_0.jpg",
    "data:image/png;base64,",
    "data:image/jpeg;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
    "data:image/png;base64,",
  ];

  return (
    <AppContainer>
      <Wrapper>
        {/* <Text className="mb-100">Our Clients</Text>
        <Note>Our customers have gotten offers from awesome companies.</Note> */}
        <div className="heading_block text-center">
          <div
            className="heading_focus_text has_underline d-inline-flex"
            style={{
              backgroundImage:
                'url("./images/shapes/shape_title_under_line.svg")',
            }}
          >
            Our Clients
          </div>
          <h2 className="heading_text mb-0">
            Our customers have gotten offers from{" "}
            <mark> awesome companies.</mark>
          </h2>
        </div>
        <Marquee>
          <MarqueeGroup>
            {row1.map((el) => (
              <ImageGroup>
                <Image src={el} />
              </ImageGroup>
            ))}
          </MarqueeGroup>
          <MarqueeGroup>
            {row1.map((el) => (
              <ImageGroup>
                <Image src={el} />
              </ImageGroup>
            ))}
          </MarqueeGroup>
        </Marquee>
        <Marquee>
          <MarqueeGroup2>
            {row2.map((el) => (
              <ImageGroup>
                <Image src={el} />
              </ImageGroup>
            ))}
          </MarqueeGroup2>
          <MarqueeGroup2>
            {row2.map((el) => (
              <ImageGroup>
                <Image src={el} />
              </ImageGroup>
            ))}
          </MarqueeGroup2>
        </Marquee>
      </Wrapper>
    </AppContainer>
  );
}

export default Our_Clients;

const AppContainer = styled.div`
  width: 100vw;
  height: 100vh;
  color: #000000;
  overflow-x: "hidden";
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Wrapper = styled.div`
  width: 100%;
  height: fit-content;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

const Text = styled.div`
  font-size: 35px;
  font-weight: 500;
  margin-bottom: 10px;
  color: #02203c;
`;

const Note = styled.div`
  font-size: 18px;
  font-weight: 200;
  margin-bottom: 40px;
  color: #7c8e9a;
`;

const Marquee = styled.div`
  display: flex;
  width: 1200px;
  overflow: hidden;
  user-select: none;

  mask-image: linear-gradient(
    to right,
    hsl(0 0% 0% / 0),
    hsl(0 0% 0% / 1) 10%,
    hsl(0 0% 0% / 1) 90%,
    hsl(0 0% 0% / 0)
  );
`;

const scrollX = keyframes`
  from {
    left: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
`;

const common = css`
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  white-space: nowrap;
  width: 100%;
  animation: ${scrollX} 30s linear infinite;
`;

const MarqueeGroup = styled.div`
  ${common}
`;
const MarqueeGroup2 = styled.div`
  ${common}
  animation-direction: reverse;
  animation-delay: -3s;
`;

const ImageGroup = styled.div`
  display: grid;
  place-items: center;
  width: clamp(10rem, 1rem + 40vmin, 30rem);
  padding: calc(clamp(10rem, 1rem + 30vmin, 30rem) / 10);
`;

const Image = styled.img`
  object-fit: contain;
  width: 100%;
  height: 100%;
  /* border: 1px solid black; */
  border-radius: 0.5rem;
  aspect-ratio: 16/9;
  padding: 5px 20px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
`;
